



























import nowPlay from '@/components/nowPlay.vue';
export default {
	data() {
		return {
			id: '',
			playlist: '',
			playlistDetailSwith: false
		}
	},
	components: {
		nowPlay
	},
	onLoad: function(e) {
		//console.log(e);
		this.id = e.id;
		this.getPlaylistDetail(); //获取歌单详情
	},
	methods: {
		getPlaylistDetail() {
			//获取mv详情
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.playlistDetail,
				data: {
					id: _this.id
				},
				success: (res) => {
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.playlist = res.data.playlist;
						uni.hideLoading();
					} else {
						uni.hideLoading();
						console.error("数据获取失败");
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: (err) => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			})
		},
		toPlay(id){
			//跳到音乐播放页面
			var id = id;
			uni.navigateTo({
				url: '../play/play?id='+id
			});
		}
	}
}
